import React, { Component } from 'react';
import Menu from './Menu';
import $ from 'jquery';

export default class Nosotros extends Component{
    componentDidMount() {
        window.addEventListener('resize', this.resizeWindow);
        this.resizeWindow();
    }

    resizeWindow(){
        if(location.href.indexOf('nosotros') >= 0 && window.innerHeight > 700){
            setTimeout(() => {
                let minHeightMainSection = (window.innerHeight - parseInt($('.header').height(), 0) - parseInt($('.header').css('padding-top'), 0) - parseInt($('.header').css('padding-bottom'), 0));
                minHeightMainSection = (minHeightMainSection / 2 + (minHeightMainSection / 3));
                $('.wrapper-blue').css('min-height', `${minHeightMainSection}px`);
            }, 100);
        }else{
            $('.wrapper-blue').css('min-height', `inherit`);
        }
    }

    render(){
        return(
            <main className="page page-nosotros">
                <Menu />
                <section>
                    <div className="wrapper wrapper-blue">
                        <div className="container">
                            <p className="main-text white-text center">
                                Somos un grupo de asesores en el ámbito financiero legal jurídico y educativo que busca mejorar la calidad de vida de nuestros usuarios dando liquidez y crecimiento personal.
                            </p>
                            <p className="main-text white-text center not-margin-bottom">
                                <upper className="big">En GO Gestión de oportunidades SAS</upper>
                            </p>
                            <p className="main-text white-text center">
                                desarrollamos productos y servicios que promuevan la inclusión financiera y el accesi al crédito de personas naturales, pequeñas y medianas empresas con obligaciones reportadas.
                            </p>
                        </div>
                        <div className="button button-green">
                            <img src="images/button_team.png" alt="Nuestro Equipo"/>
                        </div>
                    </div>
                </section>
            </main>
        )
    }
}


