import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import Logo from './Logo';

export default class Menu extends Component{
    showMenu(){
        this.refs.iconMenu.classList.toggle('active');
        this.refs.navigator.classList.toggle('active');
    }

    render(){
        return(
            <header className="header">
                <Logo name="GO Finanzas" type={this.props.type} />
                <div className="iconMenu" ref="iconMenu" onClick={e => this.showMenu(e)}>
                    <i className="fa fa-navicon"></i>
                    <i className="fa fa-close"></i>
                </div>
                <navigator ref="navigator">
                    <ul>
                        <li>
                            <NavLink activeClassName="active" to="/goFinanzas">GO Finanzas</NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="active" to="/goFactoring">GO Factoring</NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="active" to="/goEducacion">GO Educación</NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="active" to="/nosotros">Nosotros</NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="active" to="/contactenos">Contáctenos</NavLink>
                        </li>
                    </ul>
                </navigator>
            </header>
        )
    }
}


