import React, { Component } from 'react';
import Menu from './Menu';
import $ from 'jquery';
import ajax from '../utils/ajax';

export default class Contactenos extends Component{

    componentDidMount(){
        $(() => {
            $('.input-field input, .input-field textarea').focus(e => {
                let el = e.target,
                    label = $(el).next();
                label.addClass('active');
            });

            $('.input-field input, .input-field textarea').blur(e => {
                let el = e.target,
                    value = $(el).val(),
                    label = $(el).next();
                if(value === '') label.removeClass('active');
            });

            window.addEventListener('resize', this.resizeWindow);
            this.resizeWindow();
        });
    }

    resizeWindow(){
        if(location.href.indexOf('contactenos') >= 0){
            let minHeightMainSection = window.innerHeight - parseInt($('.header').height(), 0) - parseInt($('.header').css('padding-top'), 0) - parseInt($('.header').css('padding-bottom'), 0);
            $('.header').next().find('> div').css('min-height', `${minHeightMainSection}px`);
        }
    }

    sendContact(e){
        e.preventDefault();

        let { refs } = this;

        // validamos los campos
        if(!refs.name.value){
            alert('Ingrese un nombre valido!');
        }else if(!refs.email.value){
            alert('Ingrese un email valido!');
        }else if(!refs.telephone.value){
            alert('Ingrese un telefono valido!');
        }else if(!refs.message.value){
            alert('Ingrese un mensaje valido!');
        }else{

            ajax(
                'http://zeusconsultores.com/go/sendEmail.php',
                'POST',
                {
                    name: refs.name.value,
                    email: refs.email.value,
                    telephone: refs.telephone.value,
                    message: refs.message.value,
                },
                (response) => {
                    
                    var dataResponse = JSON.parse(response);

                    if(!dataResponse){
                        return;
                    }

                    if(dataResponse.status){
                        $('.validate').removeClass('valid').next().removeClass('active');
                        refs.formContact.reset();
                    }

                    alert(dataResponse.message);
                },
                (error) => {
                    console.warn(error);
                }
            );
        }
    }

    render(){
        return(
            <main className="page page-contactenos">
                <Menu />
                <section>
                    <div className="wrapper wrapper-blue">
                        <div className="container">
                            <div className="row">
                                <div className="col s12 m12 l6">
                                    <h1 className="upper white-text">Para consultas por favor llámenos o envie un email:</h1>
                                    <p className="white-text">
                                        Cel. <a href="tel:3113343436" className="white-text"><i className="fa fa-mobile"></i> 311 334 34 34</a> / Tel. <a href="tel:8911107" className="white-text"><i className="fa fa-phone"></i> (6) 891 11 07</a>
                                    </p>
                                    <p className="white-text">
                                        <a href="mailto:comercial@gofinanzas.com" className="white-text"><i className="fa fa-envelope"></i> comercial@gofinianzas.com</a>
                                    </p>
                                    <h1 className="upper white-text">Nuestra dirección</h1>
                                    <p className="white-text">Calle 62 No. 23 - 61</p>
                                    <p className="white-text">Edificio Torre Plaza 62 Ofic. 402</p>
                                    <p className="white-text">Manizales - Caldas</p>
                                    <h1 className="upper white-text">Visitenos en Redes</h1>
                                    <p className="white-text">Facebook: <a href="https://www.facebook.com/GO-Finanzas-1637715529826542/" target="_blank" className="white-text"><i className="fa fa-facebook-square"></i> GO Finanzas</a></p>
                                    <h1 className="upper white-text">Alternativa</h1>
                                    <p className="white-text">Puede diligenciar esta información y nos pondremos en contacto con usted:</p>
                                </div>
                                <div className="col s12 m12 l6">
                                    <form ref="formContact" onSubmit={e => this.sendContact(e)}>
                                        <div className="input-field">
                                            <input id="name" ref="name" type="text" className="validate" required />
                                            <label htmlFor="name"><i className="fa fa-user"></i> Nombre</label>
                                        </div>
                                        <div className="input-field">
                                            <input id="telephone" ref="telephone" type="tel" className="validate" required />
                                            <label htmlFor="number"><i className="fa fa-phone"></i> Número Telefónico</label>
                                        </div>
                                        <div className="input-field">
                                            <input id="email" ref="email" type="email" className="validate" required />
                                            <label htmlFor="email"><i className="fa fa-envelope"></i> Dirección de Email</label>
                                        </div>
                                        <div className="input-field">
                                            <textarea id="comment" ref="message" className="materialize-textarea validate" required></textarea>
                                            <label htmlFor="comment"><i className="fa fa-comment"></i> Comentario</label>
                                        </div>
                                        <div className="container_btn">
                                            <button className="btn btn-primary btn-form btn-block">Enviar <i className="fa fa-send"></i></button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        )
    }
}


