import React, { Component } from 'react';
import Menu from './Menu';
import OwlCarousel from 'react-owl-carousel';

export default class Home extends Component{
    render(){
        return(
            <main className="page page-home">
                <Menu />
                <section>
                    <OwlCarousel 
                        className="owl-theme" 
                        loop={true} 
                        margin={0} 
                        items={1} 
                        nav={true} 
                        navText={['<','>']} 
                        dots={false}  
                        autoplay={true} 
                        autoplayHoverPause={true} 
                        autoplayTimeout={8000}
                        info={true}>

                        <div className="item">
                            <img src="./images/inicio.jpg" alt="Inicio"/>
                        </div>
                        <div className="item">
                            <img src="./images/inicio2.jpg" alt="Inicio"/>
                        </div>
                        <div className="item">
                            <img src="./images/inicio3.jpg" alt="Inicio"/>
                        </div>
                    </OwlCarousel>
                </section>
            </main>
        )
    }
}


