import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

export default class Logo extends Component {
    render(){
        let { props } = this;
        let logo = <img src="./images/logo.png" alt={props.name} />;

        if(props.type === 'green'){
            logo = <img src="./images/logo_green.png" alt={props.name} />;
        }else if(props.type === 'blue'){
            logo = <img src="./images/logo_blue.png" alt={props.name} />;
        }

        return(
            <logo>
                <NavLink activeClassName="active" to="/">
                    {logo}
                </NavLink>
            </logo>
        )
    }
}


