import React, { Component } from 'react';
import Menu from './Menu';
import $ from 'jquery';

export default class GOEducacion extends Component{
    componentDidMount() {
        window.addEventListener('resize', this.resizeWindow);
        this.resizeWindow();
    }

    resizeWindow(){
        if(location.href.indexOf('goEducacion') >= 0 && window.innerHeight > 700){
            setTimeout(() => {
                let minHeightMainSection = window.innerHeight - parseInt($('.header').height(), 0) - parseInt($('.header').css('padding-top'), 0) - parseInt($('.header').css('padding-bottom'), 0) - parseInt($('.wrapper-white img').height(), 0) - 2;
                $('.wrapper-blue').css('min-height', `${minHeightMainSection}px`);
            }, 100);
        }else{
            $('.wrapper-blue').css('min-height', `inherit`);
        }
    }

    render(){
        return(
            <main className="page page-go-educacion">
                <Menu type="blue" />
                <section>
                    <div className="wrapper wrapper-blue not-padding-bottom">
                        <div className="container">
                            <p className="main-text main-text-padding white-text center">
                                Creemos en la educación en crecimiento y finanzas personales aportan conocimientos importantes a nuestros usuarios.
                            </p>
                            <p className="main-text main-text-padding white-text center">
                                Contamos con herramientas diseñadas para educar a los usuarios en planeación y manejo del dinero, asi desarrollan buenos hábitos de pago.
                            </p>
                            <p className="main-text main-text-padding white-text center">
                                Promovemos la educación formal por medio de aliados estratégicos en cursos y eventos de capacitación gratuitos.
                            </p>
                        </div>
                    </div>
                    <div className="wrapper wrapper-white not-padding-bottom not-padding-top">
                        <img src="./images/educacion.jpg" alt="GO Educación"/>
                    </div>
                </section>
            </main>
        )
    }
}


