import React, { Component } from 'react';
import Menu from './Menu';
import $ from 'jquery';

export default class GOFactoring extends Component{
    componentDidMount() {
        window.addEventListener('resize', this.resizeWindow);
        this.resizeWindow();
    }

    resizeWindow(){
        if(location.href.indexOf('goFactoring') >= 0 && window.innerHeight > 700){
            setTimeout(() => {
                let minHeightMainSection = window.innerHeight - parseInt($('.header').height(), 0) - parseInt($('.header').css('padding-top'), 0) - parseInt($('.header').css('padding-bottom'), 0) - parseInt($('.wrapper-blue').height(), 0) - (116 / 2);
                $('.wrapper-white').css('min-height', `${minHeightMainSection}px`);
            }, 100);
        }else{
            $('.wrapper-white').css('min-height', `inherit`);
        }
    }

    render(){
        return(
            <main className="page page-go-factoring">
                <Menu type="green" />
                <section>
                    <div className="wrapper wrapper-white">
                        <div className="container">
                            <div className="row">
                                <div className="col s12 m5">
                                    <img src="./images/somos.png" alt="Somos el factor de crecimiento para nuestros aliados."/>
                                </div>
                                <div className="col s12 m7">
                                    <img src="./images/asi-lo-hacemos.png" alt="Asi lo hacemos"/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="wrapper wrapper-blue">
                        <div className="container">
                            <div className="list list-factoring">
                                <div className="list-item">
                                    Facilitamos y agilizamos la recuperación de cartera.
                                </div>
                                <div className="list-item">
                                    Ahorramos tiempo y costos de operació.
                                </div>
                                <div className="list-item">
                                    Minimizamos riesgos de impago y asumimos el riesgo de insolvencia
                                </div>
                                <div className="list-item">
                                    Reducimos plazos de pago y dando liquidez inmediata a nuestros aliados.
                                </div>
                                <div className="list-item">
                                    Facilitamos un eficaz control estadístico y contable de la cartera.
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        )
    }
}


