import React, { Component } from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';

import Home from './Components/Home';
import Intro from './Components/Intro';
import GOFinanzas from './Components/GOFinanzas';
import GOFactoring from './Components/GOFactoring';
import GOEducacion from './Components/GOEducacion';
import Nosotros from './Components/Nosotros';
import Contactenos from './Components/Contactenos';

export default class App extends Component{
  constructor(props){
    super(props);

    this.state = {
      sawIntro: localStorage.readIntroFirst || false
    }
  }

  componentWillMount() {
    if(!this.state.sawIntro){
      this.setState({
        sawIntro: true
      });
      window.location = '#/intro';
    }
  }

  render(){
    return(
      <Router>
        <div>
          <Route exact path="/" component={Home} />
          <Route path="/intro" component={Intro} />
          <Route path="/goFinanzas" component={GOFinanzas} />
          <Route path="/goFactoring" component={GOFactoring} />
          <Route path="/goEducacion" component={GOEducacion} />
          <Route path="/nosotros" component={Nosotros} />
          <Route path="/contactenos" component={Contactenos} />
        </div>
      </Router>
    )
  }
}



