import React, { Component } from 'react';

import Menu from './Menu';

export default class GOFinanzas extends Component{
    render(){
        return(
            <main className="page page-go-finanzas">
                <Menu />
                <section>
                    <div className="wrapper wrapper-blue">
                        <div className="container">
                            <p className="main-text main-text-padding white-text center">
                                El servicio se basa en un modelo educativo y asesoría en el ámbito financiero, legal y jurídico que permita reconstruir el historial crediticio de nuestros usuarios y recuperar su tranquilidad.
                            </p>
                            <p className="main-text main-text-padding white-text center">
                                <upper>Nuestro Objetivo</upper> es ubicaelos nuevamente en una entidad financiera con una obligación sin reportes negativos ajustada a sus necesidades.
                            </p>
                            <p className="main-text main-text-brackets white-text center">
                                Aplica para <upper>Activos y Pensionados</upper> de nómina del estado <upper>o Empresas Privadas</upper> que tengan convenios con entidades bancarias para creditos por libranzas.
                            </p>
                        </div>
                        <div className="button button-green">
                            <img src="images/button.png" alt="Asi lo Hacemos"/>
                        </div>
                    </div>
                    <div className="wrapper wrapper-white">
                        <div className="container">
                            <div className="list list-objetives">
                                <div className="list-item">
                                    Aportamos el capital para el pago de deudas vencidas, inculidas tarjetas de crédito.
                                </div>
                                <div className="list-item">
                                    Reorganizamos deudas descontadas por nómina en una sola obligación con tasa de interés mejorada.
                                </div>
                                <div className="list-item">
                                    Gestionamos descuentos hasta del 50% en cartera vencida.
                                </div>
                                <div className="list-item">
                                    BRINDAMOS LIQUIDEZ.
                                </div>
                            </div>
                            <p className="main-text main-text-quotes center">
                                <upper>El monto del servicio más la obligación negociada nunca serán superiores a la obligación inicial reportada</upper>
                            </p>
                        </div>
                    </div>
                </section>
            </main>
        )
    }
}


