import React, { Component } from 'react';

import Logo from './Logo';

export default class Intro extends Component{
    componentDidMount() {
        localStorage.readIntroFirst = true;
    }

    render(){
        return(
            <main className="page page-intro intro valign-wrapper">
                <div className="container">
                    <div className="intro-wrapper valign center">
                        <p className="intro-text main-text">
                            En GO somos positivos, los problemas son retos, no obstáculos. Compartimos los triunfos, trabajamos en equipo, amamos lo que hacemos, somos proactivos, nos ponemos en su lugar.
                        </p>
                        <div className="intro-logo center">
                            <Logo />
                        </div>
                    </div>
                </div>
            </main>
        )
    }
}


